import React, { Fragment, useState } from 'react';
import { connect } from 'react-redux'; // connecting with the alert action and this call is exported at the bottom
import { Link, Redirect } from 'react-router-dom';
import { setAlert } from '../../actions/alert';
import { register } from '../../actions/auth';
import PropTypes from 'prop-types'; // always import this when working with prompt

const Register = ({ setAlert, register, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    password2: '',
  });

  const { name, email, password, password2 } = formData;

  const onChange = (e) =>
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });

  const onSubmit = async (e) => {
    e.preventDefault();
    if (password !== password2) {
      setAlert('Passwords do not match', 'danger'); // calling the alert from the action
    } else {
      register({
        name,
        email,
        password,
      });
    }
  };

  if (isAuthenticated) {
    //adding from upload from here

    return <Redirect to='/dashboard' />;
  }

  return (
    <Fragment>
      <h1 className='large text-primary'> Sign Up </h1>{' '}
      <p className='lead'>
        <i className='fas fa-user' /> Create Your Account{' '}
      </p>{' '}
      <form className='form' onSubmit={onSubmit}>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Name'
            name='name'
            value={name}
            onChange={onChange}
          />{' '}
        </div>{' '}
        <div className='form-group'>
          <input
            type='email'
            placeholder='Email Address'
            name='email'
            value={email}
            onChange={onChange}
          />{' '}
          <small className='form-text'>We won't spam you </small>{' '}
        </div>{' '}
        <div className='form-group'>
          <input
            type='password'
            placeholder='Password'
            name='password'
            value={password}
            onChange={onChange}
          />{' '}
        </div>{' '}
        <div className='form-group'>
          <input
            type='password'
            placeholder='Confirm Password'
            name='password2'
            value={password2}
            onChange={onChange}
          />{' '}
        </div>{' '}
        <input type='submit' className='btn btn-primary' value='Register' />
      </form>{' '}
      <p className='my-1'>
        Already have an account ? <Link to='/login'> Sign In </Link>{' '}
      </p>{' '}
    </Fragment>
  );
};

Register.propTypes = {
  setAlert: PropTypes.func.isRequired,
  register: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, {
  // exporting connect as well
  setAlert,
  register,
})(Register);
